import './styles/style.scss'
import Plyr from 'plyr'

console.log('Works')

const movementStrength = 25
const height = movementStrength / window.innerHeight
const width = movementStrength / window.innerWidth
document.querySelector('body').addEventListener('mousemove', e => {
  const pageX = e.pageX - window.innerWidth / 2
  const pageY = e.pageY - window.innerHeight / 2
  const newvalueX = width * pageX * -1 - 25
  const newvalueY = height * pageY * -1 - 50
  document.querySelector('.stars').style.backgroundPosition = newvalueX + 'px ' + newvalueY + 'px'
})

document.querySelectorAll('audio').forEach(el => {
  console.log(el.classList)
  new Plyr('.' + el.classList[0], {})
})

const features = document.querySelectorAll('.feature')
const lineSvg = document.querySelector('.line-image')
const positionFeatures = rotated => {
  const target = rotated ? '.line-image-rotated' : '.line-image'
  console.log(document.querySelectorAll(`${target} .circle-group`))
  document.querySelectorAll(`${target} .circle-group`).forEach((g, i) => {
    const moveToLeft = rotated ? 80 : -features[i].offsetWidth / 2 + 50
    const marginTop = lineSvg.clientHeight / 3
    console.log('g.getBoundingClientRect().top', g.getBoundingClientRect())
    features[i].style.left = `${g.getBoundingClientRect().left + moveToLeft}px`
    // adding scrollY because getBoundingClientRect returns a position relative to the viewport
    features[i].style.top = `${g.getBoundingClientRect().top + marginTop + window.scrollY}px`
    features[i].style.visibility = 'visible'
    console.log('target', target)
  })
}

console.log(Math.max(window.innerWidth, document.documentElement.clientWidth))
positionFeatures(Math.max(window.innerWidth, document.documentElement.clientWidth) < 700)
window.addEventListener('scroll', () => {
  positionFeatures(Math.max(window.innerWidth, document.documentElement.clientWidth) < 700)
})
window.addEventListener('resize', () => {
  positionFeatures(Math.max(window.innerWidth, document.documentElement.clientWidth) < 700)
})
